@mixin respond-phone {
  @media (max-width: 600px) {
    @content;
  }
}

* {
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  // padding: 20px;
  background-color: black;
  font-family: 'Lora', serif;

  @include respond-phone {
    font-size: 37.5%; //
  }

